<template>
  <img
    :class="['img-thumbnail', 'rounded', { 'click': $attrs.onClick }]"
    name="photo"
    :src="srcPhoto"
    style="min-width: 70px"
  />
</template>

<script>
export default {
  name: "PbPhoto",
  props: {
    src: String,
  },
  data() {
    return {
      baseUrl: 'http://10.10.2.250:3000/',
    }
  },
  computed: {
    srcPhoto() { return this.baseUrl + this.src },
  },
  mounted() {
  },
}
</script>

<style scoped>
.click {
  cursor: pointer;
}
</style>