<template>
  <div class="search">
    <div class="flex-fill">
      <div
        style="position:relative; margin:0; padding:0; border:none;"
        @mouseover="hoverDiv = true"
        @mouseleave="hoverDiv = false"
      >
        <div v-if="showClear" @click="clearInput">
          <div
            class="fas fa-times"
            style="position: absolute; cursor: pointer; z-index: 0; font-size: 25px; color: rgb(170, 170, 170); top: 11.5px; right: 15px;"
          />
        </div>
        <input
          ref="search"
          type="text"
          class="form-control form-control-lg"
          placeholder="Быстрый поиск контактов"
          spellcheck="false"
          autocomplete="off"
          :value="modelValue"
          @input="updateInput"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PbSearch',
  props: {
    modelValue: String,
  },
  data() {
    return {
      hoverDiv: false,
    }
  },
  computed: {
    showClear() { return this.hoverDiv && this.modelValue.length > 0 }
  },
  methods: {
    updateInput(event) {
      this.$emit('update:modelValue', event.target.value)
    },
    clearInput() {
      this.$emit('update:modelValue', "")
      this.$refs.search.focus()
    }
  }
}
</script>

<style>
.search {
  padding-top: 48px;
  padding-bottom: 8px;
  position: -webkit-sticky;
  position: sticky;
  background-color: white;
  z-index: 1000;
  top: 37px;
}
</style>