<template>
  <div v-if="apiService">
    <pb-search v-model="filter" />
    <pb-contact-card v-if="contact" :item="contact" />
    <div class="mt-4 row p-2">
      <pb-contact
        v-for="(contact, index) in contacts"
        :item="contact"
        :key="contact.id"
        :department-title="departmentTitle(contact.department, contacts[index - 1] && contacts[index - 1].department)"
        @click="clickContact"
      />
    </div>
    <div ref="observer" class="observer">
      <scale-loader
        :loading="loading"
        :color="color"
        :height="height"
        :width="width"
        :margin="margin"
        :radius="radius"
      ></scale-loader>
    </div>
  </div>
  <div v-else class="text-center" style="margin-top: 100px">
    <h1 class="display-4">Сервис временно не доступен :-(</h1>
    <p class="lead">Загляните к нам чуть позднее. Мы его обязательно починим!</p>
  </div>
</template>

<script>
import { useMeta } from 'vue-meta'
import { get, call } from 'vuex-pathify'
import PbContact from '@/components/PbContact'
import PbSearch from '@/components/PbSearch'
import PbContactCard from '@/components/PbContactCard'
import ScaleLoader from 'vue-spinner/src/ScaleLoader'

export default {
  name: 'Contacts',
  components: {
    PbContact,
    ScaleLoader,
    PbSearch,
    PbContactCard,
  },
  data() {
    return {
      color: '#337ab7',
      height: '20px',
      width: '4px',
      margin: '2px',
      radius: '0px',
      limit: 30,
      filter: this.getUrlHashValue(),
    }
  },
  computed: {
    contacts: () => get('phonebook@contacts').value,
    contact: () => get('phonebook@contact').value,
    loading: () => get('phonebook@loading').value,
    lastFilter: () => get('phonebook@lastFilter').value,
    apiService: () => get('phonebook@apiService').value,
  },
  mounted() {
    const options = {
      rootMargin: '0px',
      threshold: 1.0
    }
    const callback = (entries, observer) => {
      if (entries[0].isIntersecting) {
        this.loadMoreContacts({ limit: this.contacts.length == 0 ? 10 : this.limit, filter: this.filter })
      }
    }
    const observer = new IntersectionObserver(callback, options)
    observer.observe(this.$refs.observer)
  },
  unmounted() {
    if (this.contacts.length > 100) {
      this.loadContacts({ limit: 10, filter: this.filter, reload: true })
    }
  },
  methods: {
    ...call('phonebook', [
      'loadMoreContacts',
      'loadContacts',
    ]),
    departmentTitle(curDep, oldDep = []) {
      for (let idx = 0; idx < curDep.length; idx++) {
        if (oldDep[idx] && oldDep[idx] == curDep[idx]) {
          continue
        } else {
          return curDep.slice(idx)
        }
      }
      return []
    },
    getUrlHashValue() {
      return decodeURIComponent(window.location.hash.slice(1))
    },
    clickContact(contact) {
      this.filter = contact.name
    },
  },
  watch: {
    filter(newValue) {
      window.location.hash = newValue
      if (newValue != this.lastFilter) {
        this.loadContacts({ limit: 10, filter: newValue, reload: true })
      }
    }
  },
  setup() {
    useMeta({
      title: 'Контакты | Телефонный справочник [версия 3.0]',
    })
  },
  created() {
    this.filter = this.lastFilter || this.getUrlHashValue()
    window.onhashchange = (event) => {
      this.filter = decodeURIComponent(event.target.location.hash.slice(1))
    }
  }
}
</script>

<style>
.observer {
  height: 30px;
}
</style>